nav {
  z-index: 100;
  position: fixed;
  left: 50%;
  bottom: 15px;
  transform: translateX(-50%);
  border-radius: 10px;
  background-color: var(--secondary-color);
  box-sizing: border-box;
  padding: 0 10px;
  /* gap: 5px; */
  /* width: 280px; */
  /* width: 100%; */
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}
nav a {
  position: relative;
  height: 50px;
  width: 55px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  /* border: 1px solid hotpink; */
}
nav a:hover span.icon {
  color: #29fd53;
  font-size: large;
}
nav a span.icon {
  position: relative;
  display: block;
  text-align: center;
  line-height: 70px;
  color: white;
  transition: ease-out 0.5s;
  display: flex;
  align-items: center;
  justify-content: center;
  backdrop-filter: blur(50px);
  border-radius: 50%;
}
nav a.active span.icon {
  transform: translateY(-30px);
  color: #29fd53;
  background-color: transparent;
  width: 75%;
  height: 80%;
  font-size: x-large;

  text-align: center;
  border: 5px solid #29fd53;
}
nav a span.text {
  letter-spacing: -0.5px;
  font-size: smaller;
  font-weight: bold;
  position: absolute;
  transition: ease-out 0.5s;
  opacity: 0;
  transform: translateY(10px);
  color: #29fd53;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.6);
}
nav a.active span.text {
  opacity: 1;
}

/* responsiveness */
@media (max-width: 550px) {
  nav {
    /* width: 100%; */
    bottom: 5px;
  }
}
@media (max-width: 280px) {
  nav {
    width: 100%;
    bottom: 0;
    border-radius: 0;
    padding: 0 2px;
  }
}
